<script lang="ts">
    import {fade} from "svelte/transition"
    import {popupAction, popupList} from "../../../util/alert";
    import {appInfo} from "../../../webapp";
    import Button from "../input/Button.svelte";


    function showAppInfo(){
        popupList(
            "",
            [
                {icon: "", text: " : " + (appInfo.version ?? "")},
                {icon: "", text: " : "  + (appInfo.updateDate ?? "")},
                {icon: "", text: ": "        + (appInfo.technologistName ?? "")}
            ],
            appInfo.comment ?? ""
        )
    }

    function showHelpDownloader(){
        popupAction(
            "",
            appInfo.description ?? "",
            " ",
            () => downloadUserManual(appInfo.helpPath ?? "")
        )
    }

    function downloadUserManual(href: string){
        const link = document.createElement('a')
        link.href = href
        link.target = '_blank'
        document.body.appendChild(link)
        link.click()
        document.body.removeChild(link)
    }

</script>

<header id="header">
    <a href="{appInfo.groupPath}" transition:fade>
        {appInfo.groupName}
    </a>|<p transition:fade>
        {appInfo.name}
    </p>
    <Button frameless hint=""                    image="reset.svg" on:click={() => location.reload()}/>
    <Button frameless hint="  "  image="info.svg"  on:click={showAppInfo}/>
    <Button frameless hint=" " image="help.svg"  on:click={showHelpDownloader}/>
</header>